} */

.contai {
    max-width: 500px;
    margin: 20px auto 0 auto;
    padding: 30px;
    background-color: aquamarine;

}

.head {
    font-size: 50px;
    font-family: 'Times New Roman', Times, serif;
    margin: auto 100px auto;
    color: blue;
}

.crea {
    display: flex;
}

.crea input {
    flex: 1;
    padding: 10px;
    border-radius: 10px;
}

.down {
    margin-top: 10px;
}

.subm {
    border-radius: 10px;
    flex: 1;
    margin-top: 5px;
    padding: 5px;
    background-color: green;
    color: white;

}
.subm:hover{
    background-color: orange;
}

.none {
    text-decoration: none;
    color: blue;
}

.sp {
    text-align: center;
    margin-top: 10px;
}






