* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

/* body{
    display: flex;
    justify-content: center;
} */
.index {

    max-width: 600px;
    margin: 20px auto 0 auto;
    padding: 30px;
    background-color: whitesmoke;
}

.index h1,
p {
    margin: 15px 30px;
}

.first {
    margin: 15px 30px;
    

}

.first_1 {
    padding: 10px;
    text-align: center;
    border-radius: 5px;
}


.and {
    display: flex;
}

.second {
    flex: 1;
    margin-top: 10px;
    padding: 10px;
    width: 390px;
    border-radius: 5px;
}

.birth {
    margin-left: 30px;
}

.three {
    margin: 5px 30px;
    display: flex;
    column-gap: 5px;
}

.con {
    width: 60px;
    padding: 5px;
    border-radius: 5px;
}

.pot {
    font-size: 13px;
    margin: 0 2px;
    color: blue;
}

.rad {
    margin: 10px 30px;
}

.rad_1 {
    margin-left: 20px;
}

.sig {
    margin: 15px 30px;
}

.sig_1 {
    padding: 5px;
    width: 140px;
    text-align: center;
    background-color: green;
    color: white;
    border-radius: 5px;
}